<template>
  <div>
    <div class="row m-r-xs m-l-xs m-b">
      <button class="pull-left md-btn blue w-sm" @click="openModal('create')">{{ $t('action.create_object', { object: $t('setting.announcement')}) }}</button>
      <div class="pull-right">
        <button
          class="md-btn w-sm blue"
          @click="changeMode"
          v-show="announcements.length > 1"
        >{{ mode ? $t('action.adjust_rank') : $t('action.confirm') }}</button>
        <button
          class="md-btn w-sm m-l-sm"
          v-show="!mode"
          @click="cancelAdjustRank"
        >{{ $t('action.cancel') }}</button>
      </div>
    </div>

    <div class="box">
      <table st-table="rowCollectionBasic" class="table table-striped">
        <thead>
          <tr>
            <th style="width: 3%;" v-show="!mode"></th>
            <th>{{ $t('setting.announcement') + $t('common.content') }}</th>
            <th class="text-center">{{ $t('common.status') }}</th>
            <th class="text-center" style="width: 10%;">{{ $t('common.operate') }}</th>
          </tr>
        </thead>
        <draggable
          v-model="announcements"
          :element="'tbody'"
          :options="{disabled:mode}"
          v-if="!pageLoading && announcements.length > 0"
        >
          <tr
            v-for="(element, index) in announcements"
            :key="element.id"
          >
            <td v-show="!mode"><i class="fa fa-reorder text-blue"></i></td>
            <td>{{ element.content }}</td>
            <td class="text-center p-r-0">
              <span
                  class="label m-r-sm"
                  :class="element.status ? 'success' : 'danger'"
              >{{ element.status ? $t('status.active') : $t('status.inactive') }}
              </span>
              <label class="md-switch">
                  <input
                      type="checkbox"
                      v-model="element.status"
                      @change="updateAnnouncement({ mode: 'update', index, key: 'status'})"
                  />
                  <i class="success"></i>
              </label>
            </td>
            <td class="text-center">
              <button class="btn btn-xs btn-icon blue" @click="openModal('update', element, index)">
                  <i class="fa fa-edit"></i>
              </button>
              <button class="btn btn-xs btn-icon danger" @click="openModal('delete', element, index)">
                  <i class="fa fa-trash-o"></i>
              </button>
            </td>
          </tr>
        </draggable>
      </table>

      <div class="row" v-if="pageLoading">
        <div class="text-center m-a">
          <i class="fa fa-spin fa-spinner"></i> <b>{{ $t('common.loading') }}</b>
        </div>
      </div>

      <div class="row" v-if="!pageLoading && !announcements.length">
        <div class="text-center m-a">
          <span>{{ $t('common.no_data') }}</span>
        </div>
      </div>
    </div>

    <div class="modal" v-if="modal.showModal">
      <div class="modal-backdrop fade in" @click="modal.showModal = false">
      </div>
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">{{ modal.mode && $t('action.' + modal.mode) }}{{ $t('setting.announcement') }}</h5>
          </div>
          <div class="modal-body p-lg">
            <form v-if="modal.mode === 'create' || modal.mode === 'update'">
              <label
                class="form-control-label p-b-0"
                :class="{'text-blue': modal.announcement.content}"
              >{{ $t('setting.announcement') + $t('common.content') }}
              </label>
              <textarea
                v-model.trim="modal.announcement.content"
                class="form-control"
                ref="modalContent"
              />
            </form>
            <span v-else>
              {{ $t('message.sure_to_do', {
                operate: $t('action.' + modal.mode),
                object: $t('setting.announcement')
              }) }}
            </span>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn dark-white p-x-md w-xs"
              @click="modal.showModal = false"
            >
              {{ $t('action.cancel') }}
            </button>
            <button
              type="button"
              class="btn blue p-x-md w-xs"
              @click="updateAnnouncement({
                mode: modal.mode,
                index: modal.index,
                key: 'content'
              })"
              :disabled="!modal.announcement.content"
            >
              <span v-if="modal.loading"><i class="fa fa-spin fa-spinner"></i></span>
              <span v-else>{{ $t('action.confirm') }}</span>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import api from '../api'
import $ from '../utils/util'
import Draggable from 'vuedraggable'
export default {
  data () {
    return {
      pageLoading: true,
      mode: true,
      announcements: [],
      modal: {
        index: '',
        showModal: false,
        mode: '',
        announcement: {
          id: '',
          content: ''
        },
        loading: false
      }
    }
  },
  created () {
    this.getAnnouncementsList()
  },
  methods: {
    getAnnouncementsList () {
      this.$http.get(api.announcement).then(data => {
        this.announcements = Object.assign([], data.sort((a, b) => a.rank - b.rank))
        this.pageLoading = false
      })
    },
    openModal (mode = '', {id, content} = {id: '', content: ''}, index = '') {
      this.modal = Object.assign({
        index,
        mode,
        announcement: {
          id,
          content
        },
        showModal: true,
        loading: false
      })
      if (mode === 'create' || mode === 'update') {
        this.$nextTick(() => {
          this.$refs.modalContent.focus()
          this.$refs.modalContent.select()
        })
      }
    },
    updateAnnouncement ({ mode = '', index = '', key = '' }) {
      this.modal.loading = true
      if (mode === 'create') {
        this.$http.post(api.announcement, {
          content: this.modal.announcement.content
        }).then(data => {
          this.announcements = [data, ...this.announcements]
          this.notify(mode)
          this.modal.showModal = false
        })
      } else if (mode === 'update') {
        let result = {}
        result[key] = key === 'status' ? this.announcements[index][key] : this.modal.announcement[key]
        this.$http.put(`${api.announcement}${this.announcements[index].id}/`, result).then(data => {
          this.$set(this.announcements, index, data)
          this.notify(mode)
          this.modal.showModal = false
        })
      } else if (mode === 'delete') {
        this.$http.delete(`${api.announcement}${this.announcements[index].id}/`).then(() => {
          this.announcements.splice(index, 1)
          this.notify(mode)
          this.modal.showModal = false
        })
      }
    },
    changeMode () {
      if (!this.mode) {
        this.$http.post(`${api.announcement}rank/`, this.announcements.map((element, index) => Object({
          id: element.id,
          rank: index + 1
        }))).then(data => {
          this.announcements.forEach((element, index) => {
            element.rank = index + 1
          })
          this.notify('update')
        })
      }
      this.mode = !this.mode
    },
    cancelAdjustRank () {
      this.announcements = this.announcements.sort((a, b) => a.rank - b.rank)
      this.mode = true
    },
    notify (operate = 'update', type = 'success') {
      this.modal.loading = false
      $.notify({
        message: this.$t('action.' + operate) + this.$t('status.' + type),
        type
      })
    }
  },
  components: {
    Draggable
  }
}
</script>



